<template>
  <div id="app">
      <el-container>
          <el-header class="blues head-title"></el-header>
          <el-container>
              <el-aside width="200px" v-show="isloginPage">
                  <el-menu  @open="handleOpen"
                  :default-active="$route.path"
                  :default-openeds="openeds"
                  @close="handleClose">
                      <el-submenu index="1">
                          <template slot="title"></template>
                              <el-menu-item index="/jurisdiction"  @click="jumpEvent('jurisdiction')"></el-menu-item>
                              <el-menu-item index="/labelAdmin" @click="jumpEvent('labelAdmin')"></el-menu-item>
                      </el-submenu>
                      <el-submenu index="2">
                          <template slot="title"></template>

                              <el-menu-item index="/majorList"  @click="jumpEvent('majorList')"></el-menu-item>
                              <el-menu-item index="/coursewareList" @click="jumpEvent('coursewareList')"></el-menu-item>
                              <el-menu-item index="/testList" @click="jumpEvent('testList')"></el-menu-item>

                      </el-submenu>
                      <el-submenu index="3">
                          <template slot="title"></template>
                          <el-menu-item-group>
                              <el-menu-item index="/teamList"  @click="jumpEvent('teamList')"></el-menu-item>
                              <el-menu-item index="/teamAttribute" @click="jumpEvent('teamAttribute')"></el-menu-item>
                          </el-menu-item-group>
                      </el-submenu>
                      <el-submenu index="4">
                          <template slot="title"></template>
                          <el-menu-item-group>
                              <el-menu-item index="/circleList" @click="jumpEvent('circleList')"></el-menu-item>
                              <el-menu-item index="/circleType" @click="jumpEvent('circleType')"></el-menu-item>
                          </el-menu-item-group>
                      </el-submenu>
                      <el-submenu index="5">
                          <template slot="title"></template>
                          <el-menu-item-group>
                              <el-menu-item index="/postList"  @click="jumpEvent('postList')"></el-menu-item>
                          </el-menu-item-group>
                      </el-submenu>
                      <el-submenu index="6">
                          <template slot="title"></template>
                          <el-menu-item-group>
                              <el-menu-item index="userAdministration" @click="jumpEvent('userAdministration')"></el-menu-item>
                              <el-menu-item index="/operateAdministration" @click="jumpEvent('operateAdministration')"></el-menu-item>
                          </el-menu-item-group>
                      </el-submenu>
                  </el-menu>
              </el-aside>
              <el-main>
                  <router-view/>
              </el-main>
          </el-container>
      </el-container>
  </div>
</template>

<script>
export default {
    data(){
        return{
            openeds:[],
            menuList:[],
            isloginPage:false
        }
    },
    methods:{
        jumpEvent(type){
            this.$router.push("/"+type)
        },
        handleOpen(key, keyPath){
            console.log(key, keyPath);
        },
        handleClose(key, keyPath){
            console.log(key, keyPath);
        },
        menusEvent(){
            //http://admin.suishi.com/admin/admin_user/menus
            this.menuList=[]
        }
    },
    watch:{
        $route(to,from){
            var self = this;
            if(to.name == "login"){
                self.isloginPage = false;
            }else{
                self.isloginPage = true;
            }
        }
    },
    mounted:function(){
        var self = this;
        if(this.$route.name == "login"){
            self.isloginPage = false;
        }else{
            self.isloginPage = true;
        }
    }
}
</script>

<style>
    body{
    margin:0;

    }
    ul li{
    list-style: none;
    }
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}
    .blues{
        background:skyblue;
    }
    .head-title{
    line-height: 300%;
    color: #fff;
    font-size: 20px;
    font-weight: bold;
    }
</style>



